/*jshint -W081 */

var lncd = {};

lncd.modules = lncd.modules || {};
lncd.components = lncd.components || {};
lncd.services = lncd.services || {};

lncd.services.ResizeManager = (function () {
    'use strict';
    var ResizeManager = {};

    ResizeManager.breaks = {
        NARROW: 320,
        NARROWER: 480,
        MEDIUM: 768,
        WIDE: 1025,
        WIDEST: 1440
    };

    ResizeManager.events = {
        RESIZE: 'resize',
        CHANGE: 'change'
    };

    ResizeManager.matchSize = function (breakpoint) {
        var mq = window.matchMedia('only screen and (min-width:' + breakpoint + 'px)');
        return mq.matches;
    };

    return ResizeManager;

} ());

lncd.services.ParallaxScrolling = (function ($, window) {
    'use strict';
    var ParallaxScrolling = {},
        selectors = {
            SCROLLING_ENABLED: '.scrolling-enabled'
        },
        consts = {
            SPEED: 'speed',
            OFFSET: 'offset'
        },
        events = {
            SCROLL: 'scroll'
        },
        $window = $(window),
        _self;

    ParallaxScrolling.init = function (onScrollCallbackFn) {
        var $scrollBackgrounds = $(selectors.SCROLLING_ENABLED);

        _self = this;
        _self.onScrollActiveCallback = onScrollCallbackFn;
        _self.animateParallaxBackgrounds($scrollBackgrounds);
    };

    ParallaxScrolling.animateParallaxBackgrounds = function ($backgrounds) {
        $backgrounds.each(function () {
            var $bgElement = $(this);

            $window.on(events.SCROLL, function () {
                var speed = $bgElement.data(consts.SPEED),
                    offset = $bgElement.data(consts.OFFSET) || 0,
                    yPosition = ($window.scrollTop() / speed) - offset,
                    coords = '50% ' + yPosition + 'px';

                $bgElement.css({ backgroundPosition: coords });

                _self.onScrollActiveCallback($bgElement);
            });
        });
    };

    return ParallaxScrolling;

}(jQuery, window));


lncd.modules.Navigation = (function ($, ResizeManager, ParallaxScrolling, window) {
    'use strict';
    var Navigation = {},
        selectors = {
            HTML: 'html',
            BODY: 'body',
            ANCHOR: 'a',
            LIST: 'li',
            MAIN_NAV: '.main-nav',
            NAV_LINKS: '.main-nav-links',
            SLIDE: '.slide',
            SCROLL_ELEMENT: '.scrolling-enabled'
        },

        events = {
            CLICK: 'click',
            POP_STATE: 'popstate',
            LOAD: 'load'
        },
        classes = {
            SELECTED: 'selected'
        },

        $window = $(window),
        $mainNav = $(selectors.MAIN_NAV),
        $navLinks = $(selectors.NAV_LINKS).find(selectors.ANCHOR),
        $slides = $(selectors.SLIDE),

        arr = [0],
        count = 0,

        currentView,
        currentIndex,

        scrollOfffset,

        _self;

    Navigation.views = Navigation.views || {};

    Navigation.views.mobile = (function () {
        var view = {},
            selectors = {
                ICON_MENU: '.icon-menu'
            },
            classes = {
                EXPANDED: 'expanded'
            },
            $navButton = $(selectors.ICON_MENU),
            _self;

        view.init = function () {
            _self = this;
            scrollOfffset = 0;
            $navLinks.on(events.CLICK, _self.navigate);
            $navButton.on(events.CLICK, _self.toggleMenu);
        };

        view.clear = function () {
            scrollOfffset = null;
            $navLinks.off(events.CLICK, _self.navigate);
            $navButton.off(events.CLICK, _self.toggleMenu);
            $mainNav.removeClass(classes.EXPANDED);
        };

        view.toggleMenu = function () {
            $mainNav.toggleClass(classes.EXPANDED);
            $navButton.toggleClass(classes.EXPANDED);
        };

        view.navigate = function (evt) {
            if(evt) {
                evt.preventDefault();
            }
            currentIndex = $(this).parent().index();
            _self.toggleMenu();
            _self.scrollTo(currentIndex, null, 0);
        };

        return view;
    }());

    Navigation.views.tablet = (function () {
        var view = {},
            selectors = {
                HOME_SLIDE: '#home'
            },
            classes = {
                FIXED_TOP: 'fixed-top'
            },
            $firstSlide = $(selectors.HOME_SLIDE),
            _self;

        view.init = function () {
            _self = this;
            scrollOfffset = $mainNav.height();
            $navLinks.on(events.CLICK, _self.navigate);
        };

        view.clear = function () {
            scrollOfffset = null;
            $navLinks.off(events.CLICK, _self.navigate);
        };

        view.togglePosition = function ($window) {
            var firstElementHeight = $firstSlide.height()/ 2;

            if ($window.scrollTop() >= firstElementHeight) {
                $mainNav.addClass(classes.FIXED_TOP);
                return;
            }
            $mainNav.removeClass(classes.FIXED_TOP);
        };

        view.navigate = function (evt) {
            if (evt) {
                evt.preventDefault();
            }
            currentIndex = $(this).parent().index();
            _self.scrollTo(currentIndex);
        };

        return view;
    }());

    Navigation.init = function () {
        _self = this;
        _self.switchView();

        if (_self.getView().togglePosition) {
            _self.getView().togglePosition($window);
        }

        $window.on(events.LOAD, function () {
            _self.initRoute();
            _self.scrollTo(currentIndex);
            _self.setSlideFocusTo(currentIndex);
        });

        $window.on(ResizeManager.events.RESIZE, _self.switchView);
        $window.on(events.POP_STATE, function () {
            // console.log("location: " + document.location + ", state: " + JSON.stringify(event.state));
            _self.scrollTo(event.state.page - 1, 0);
        });

        ParallaxScrolling.init(_self.updateOnParallaxScroll);
    };

    Navigation.setView = function (type) {
        if (currentView) {
            currentView.clear();
        }
        currentView = _self.views[type];
        currentView.scrollTo = _self.scrollTo;
        currentView.init();
    };

    Navigation.getView = function () {
        if (currentView) {
            return currentView;
        }
    };

    Navigation.updateOnParallaxScroll = function ($element) {
        // toggles position when scrolling down
        if (_self.getView().togglePosition) {
            _self.getView().togglePosition($window);
        }

        _self.updateFocus($element);
    };

    Navigation.updateFocus = function ($element) {
        // sets link and path to according slide when scrolling has reached the slide
        var slideClass = selectors.SLIDE.substr(1);

        if($element.hasClass(slideClass)) {
            var slideOffset = 100,
                scrollTop = $window.scrollTop(),
                slideIndex = $element.index(selectors.SLIDE),
                $slide = $element,
                slidePositionTop = $slide.position().top - slideOffset;

            if (scrollTop >= slidePositionTop && scrollTop < slidePositionTop + $slide.height()) {
                count++;
                arr[count] = slideIndex;

                if (arr[count] !== arr[count-1]) {
                    currentIndex = slideIndex;
                    _self.setSlideFocusTo(currentIndex);
                }
            }
        }
    };

    Navigation.switchView = function () {
        if (ResizeManager.matchSize(ResizeManager.breaks.MEDIUM)) {
            Navigation.setView('tablet');
        }
        else if (ResizeManager.matchSize(ResizeManager.breaks.NARROW)) {
            Navigation.setView('mobile');
        }
    };

    /* Navigation.views.base = function () {
     var view = {};

     view.init = function () {
     console.log('base - init');
     };

     view.clear = function () {
     console.log('base - clear');
     };

     return view;
     }; */

    Navigation.setSlideFocusTo = function (index) {
        _self.selectLink(index);
        _self.setRoute(index);
    };

    Navigation.scrollTo = function (index, seconds, offset) {
        var defaultSpeed = 1.5,
            seconds = seconds || defaultSpeed, // TODO: use config
            speed = seconds * 1000,
            offset = offset || scrollOfffset,
            $html = $(selectors.HTML + ',' + selectors.BODY),
            $element = $slides.eq(index);

        $html.animate({
            scrollTop: $element.offset().top - offset
        }, speed);
    };

    Navigation.setRoute = function (index) {
        var index = index || currentIndex,
            $slide = $slides.eq(index),
            location = '#' + $slide.attr('id'),
            page = index + 1,
            title = $navLinks.eq(index).text();

        window.history.pushState({page: page}, title, location);
        window.location.hash = location;
    };

    Navigation.initRoute = function () {
        var locationString = window.location.hash.substr(1),
            currentHash = locationString || $slides.eq(0).attr('id');

        $slides.each(function(currentSlideIndex) {
            var $slide = $(this),
                slideID = $slide.attr('id');
            if (slideID === currentHash) {
                currentIndex = currentSlideIndex;
            }
        });
    };

    Navigation.selectLink = function (index) {
        var $link = $navLinks.eq(index) || $navLinks.eq(0);

        $navLinks.removeClass(classes.SELECTED);
        $link.addClass(classes.SELECTED);
    };

    return Navigation;

}(jQuery, lncd.services.ResizeManager, lncd.services.ParallaxScrolling, window));

lncd.modules.Gallery = (function ($) {
    'use strict';
    var Gallery = {},
        selectors = {
            THUMB_BUTTON_INFO: '.thumb-button-info',
            THUMB_BUTTON_CLOSE: '.thumb-button-close',
            GALLERY_THUMB_OVERLAY: '.gallery-thumb-overlay',
            GALLERY_THUMB: '.gallery-thumb'
        },
        events = {
            CLICK: 'click'
        },
        classes = {
            VISIBLE: 'visible'
        },
        $galleryThumbnailOverlay = $(selectors.GALLERY_THUMB_OVERLAY),

        _self;

    Gallery.init = function () {
        var $thumbnailInfoButton = $(selectors.THUMB_BUTTON_INFO),
            $thumbnailCloseInfoButton = $(selectors.THUMB_BUTTON_CLOSE);

        _self = this;

        $thumbnailInfoButton.on(events.CLICK, _self.toggleThumbInfoOverlay);
        $thumbnailCloseInfoButton.on(events.CLICK, _self.toggleThumbInfoOverlay);
    };

    Gallery.toggleThumbInfoOverlay = function (evt) {
        evt.preventDefault();

        var $target = $(this),
            index = $target.closest(selectors.GALLERY_THUMB).index();
        $galleryThumbnailOverlay.eq(index).toggleClass(classes.VISIBLE);
    };

    return Gallery;

} (jQuery));

lncd.modules.ContactForm = (function ($) {
    'use strict';
    var ContactForm = {},
        selectors = {
            SUBMIT_BUTTON: '#contact-form-submit',
            USER_NAME: '#name',
            USER_PHONE: '#phone',
            USER_EMAIL: '#email',
            USER_SUBJECT: '#subject',
            USER_MESSAGE: '#message'
        },
        events = {
            CLICK: 'click'
        },
        _self;

    ContactForm.init = function () {
        var $submitButton = $(selectors.SUBMIT_BUTTON);

        _self = this;
        $submitButton.on(events.CLICK, _self.submit);
    };

    ContactForm.submit = function (evt) {
        evt.preventDefault();

        var userName = $(selectors.USER_NAME).val(),
            userPhone = $(selectors.USER_PHONE).val(),
            userEail = $(selectors.USER_EMAIL).val(),
            userSubject = $(selectors.USER_SUBJECT).val(),
            userMessage = $(selectors.USER_MESSAGE).val(),
            userContactDetails = {
                name: userName,
                phone: userPhone,
                email: userEail,
                subject: userSubject,
                message: userMessage
            },
            jqxhr = $.ajax({
                url: '/sendquestion',
                type: 'POST',
                data:  userContactDetails
            });

        jqxhr.done(_self.submitSuccessHandler);
        jqxhr.fail(_self.submitFailureHandler);
    };

    ContactForm.submitSuccessHandler = function (data) {
        alert('Message have been sent');
    };

    ContactForm.submitFailureHandler = function (data) {
        alert('Message could not be sent');
    };

    return ContactForm;

}(jQuery));

lncd.app = (function (Navigation, Gallery, ContactForm) {
    'use strict';

    var app = {};

    app.init = function () {
        Navigation.init();
        ContactForm.init();
        Gallery.init();
    };

    app.init();

    return app;

}(lncd.modules.Navigation, lncd.modules.Gallery, lncd.modules.ContactForm));
